export default {
  computed: {
    user () {
      return (this.$store.state.auth || {}).username || null
    }
  },
  methods: {
    logOut () {
      this.$store.dispatch('auth/reset').then(() => {
        this.$router.push('/login')
      })
    }
  }
}
